export default {
  name:'pay_switch',
  data() {
    return {
      avator_nameO: "",
      optionchannels: [],
      tableData: [],
      currentPage:1,
      limit:10,
      total:'',
      titleData:[
        {
           prop:"days",
          label:"日期"
        },
        {
           prop:"user_reg",
          label:"新增账号个数"
        },
        {
           prop:"pay_user_d",
          label:"首日付费账号个数"
        },
        {
           prop:"day_change",
          label:"首日付费率"
        },
        {
           prop:"pay_user_w",
          label:"首周付费账号个数"
        },
        {
           prop:"week_change",
          label:"首周付费率"
        },
      ]
    };
  },
  created() {
      this.initChannel();
      this.getData();
  },
  methods: {
    search() {
        this.getData();
    },

    //页容量变化
    handleSizeChange(num) {
        this.limit = num
        this.currentPage = 1
        this.getData()
    },

    //页码变化
    handleCurrentChange(pagenum) {
        this.currentPage = pagenum
        this.getData()
    },


     getData() {

      this.initData({
        page: this.currentPage,
        limit: this.limit,
        channel_name: this.avator_nameO === "所有渠道" ? "" : this.avator_nameO,
       
      });
    },

    //获取所有渠道
    async initChannel() {
      let Channelsdata = await this.$http.OperationMan.GetChannels();
      let changeData = [];
      Channelsdata.data.data.forEach(item => {
        changeData.push(item.name);
      });
      changeData.forEach((item, index) => {
        this.optionchannels.push({ avator_nameO: item, levelO: index });
      });
      this.optionchannels.unshift({ avator_nameO: "所有渠道", levelO: -1 });
    },

    //获取表格数据
    async initData(params) {
      let { data } = await this.$http.DataAnalysis.GetrPaySwitch(params);
      console.log(data);
      
      this.tableData = data.data;
      this.total = data.total;
    }
  }
};



